body {

  font-family: 'Helvetica';

}

h1,

h2,

h3 {

  margin: 0.5rem 0;

  text-align: center;

  color: #202020;

}

h1 {

  font-size: 1.5rem;

}

h2 {

  font-size: 1.2rem;

}

h3 {

  font-size: 1rem;

}

a {

  text-decoration: none;

}

.row {

  display: flex;

  flex-wrap: wrap;

  justify-content: space-between;

}

.row.center {

  align-items: center;

}

.col-1 {

  flex: 1;

}

.col-2 {

  flex: 2;

}

 

.container{

  align-items: center;

  float: right;

  display: flex;

  width: 10rem;

  justify-content: space-between;

  margin: 0.1rem;

}

 

.block {

  background-color: #E9E8E6;

  padding: 1rem;

  margin: 0.5rem;

  border-radius: 0.5rem;

}

 

.text-right {

  text-align: right;

}

img.small {

  height: 8rem;

  margin:1rem;

}

button {

  font-size: 0.8rem;

  padding: 0.2rem;

  margin: 0.1rem;

  border-radius: 0.5rem;

  border: 0.1rem #202020 solid;

  background-color: #F0713A;

  width: 100%;

  cursor: pointer;

}

 

button:active{

  background-color: #d66e41;

}

 

button.add {

  background-color: #40c0f0;

  width: 1.6rem;

  font-size: 0.8rem;

}

 

button.remove {

  background-color: #649263;

  width: 1.6rem;

  font-size: 0.8rem;

}

button.badge {

  background-color: #F0713A;

  border: none;

  color: #ffffff;

  width: 1.5rem;

}

 

#basket{

  display: flex;

  flex-wrap: wrap;

  justify-content: space-between;

}

 

#background {

  position: absolute;

  left: 0px;

  top: 0px;

  background:#202020;

  width: 100%;

}